"use client";

import Link from "next/link";
import { useEffect, useState } from "react";
import {
  FiSearch,
  FiShoppingCart,
  FiMenu,
  FiX,
  FiUser,
  FiMoon,
  FiSun,
} from "react-icons/fi";
// import { useCart } from "@/context/CartContext";
// import { useAuth } from "@/context/AuthContext";
// import useDarkMode from "@/hooks/useDarkMode";
import { useRouter } from "next/navigation";

export default function Navbar() {
  const [open, setOpen] = useState(false);
  const [hide, setHide] = useState(false);
  const [query, setQuery] = useState("");
  const [profileOpen, setProfileOpen] = useState(false);

  const router = useRouter();
  // const { cartCount } = useCart();
  // const { user } = useAuth();
  // const { dark, setDark } = useDarkMode();

  // ðŸ”¥ Hide on scroll
  useEffect(() => {
    let lastY = window.scrollY;
    const onScroll = () => {
      setHide(window.scrollY > lastY);
      lastY = window.scrollY;
    };
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  // ðŸ” Search API (debounced)
  useEffect(() => {
    if (!query) return;
    const delay = setTimeout(() => {
      router.push(`/products?search=${query}`);
    }, 500);
    return () => clearTimeout(delay);
  }, [query]);

  return (
    <header
      className={`
        fixed w-full z-50 transition-transform duration-300
        ${hide ? "-translate-y-full" : "translate-y-0"}
        bg-white dark:bg-black border-b
      `}
    >
      <div className="max-w-[1400px] mx-auto px-4">
        <div className="flex items-center justify-between h-16">

          {/* LOGO */}
          <Link href="/" className="flex items-center gap-2 font-semibold text-lg">
            <span className="w-6 h-6 bg-green-500 rounded-full text-white flex items-center justify-center">
              C
            </span>
            CricketGear
          </Link>

          {/* DESKTOP MENU */}
          <nav className="hidden md:flex gap-8 text-sm font-medium">
            {["Bats", "Balls", "Pads", "Gloves", "Helmets"].map((item) => (
              <Link
                key={item}
                href={`/products?category=${item}`}
                className="hover:text-green-500"
              >
                {item}
              </Link>
            ))}
          </nav>

          {/* RIGHT ACTIONS */}
          <div className="hidden md:flex items-center gap-4">

            {/* Search */}
            <div className="relative">
              <FiSearch className="absolute left-3 top-2.5 text-gray-400" />
              <input
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                placeholder="Search products..."
                className="pl-9 pr-3 py-1.5 rounded-full bg-green-50 dark:bg-gray-800 text-sm w-56 outline-none"
              />
            </div>

            {/* Dark mode */}
            {/* <button onClick={() => setDark(!dark)}>
              {dark ? <FiSun /> : <FiMoon />}
            </button> */}

            {/* Cart */}
            <Link href="/cart" className="relative">
              <FiShoppingCart size={18} />
              {/* {cartCount > 0 && (
                <span className="absolute -top-2 -right-2 bg-green-500 text-white text-xs w-5 h-5 rounded-full flex items-center justify-center">
                  {cartCount}
                </span>
              )} */}
            </Link>

            {/* Profile */}
            {user ? (
              <div className="relative">
                <button onClick={() => setProfileOpen(!profileOpen)}>
                  <FiUser />
                </button>

                {profileOpen && (
                  <div className="absolute right-0 mt-2 bg-white dark:bg-gray-900 border rounded-md shadow w-40 text-sm">
                    <Link href="/profile" className="block px-4 py-2 hover:bg-gray-100">
                      Profile
                    </Link>
                    <Link href="/orders" className="block px-4 py-2 hover:bg-gray-100">
                      Orders
                    </Link>
                    <button className="w-full text-left px-4 py-2 hover:bg-gray-100">
                      Logout
                    </button>
                  </div>
                )}
              </div>
            ) : (
              <>
                <Link href="/login">Login</Link>
                <Link href="/signup" className="bg-green-500 text-white px-4 py-1.5 rounded-full">
                  Sign Up
                </Link>
              </>
            )}
          </div>

          {/* MOBILE MENU */}
          <button className="md:hidden" onClick={() => setOpen(!open)}>
            {open ? <FiX /> : <FiMenu />}
          </button>
        </div>
      </div>
    </header>
  );
}
